@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

input::-webkit-input-placeholder { color: #626262; }

input::-moz-placeholder { color: #626262; }

/* Firefox 19+ */
input:-moz-placeholder { color: #626262; }

/* Firefox 18- */
input:-ms-input-placeholder { color: #626262; }

textarea::-webkit-input-placeholder { color: #626262; }

textarea::-moz-placeholder { color: #626262; }

/* Firefox 19+ */
textarea:-moz-placeholder { color: #626262; }

/* Firefox 18- */
textarea:-ms-input-placeholder { color: #626262; }

.file-input { width: 100%; }

.file-control { overflow: hidden; }

.file-control:after { content: attr(data-filename); }

.file-input { display: inline-block; text-align: left; background: #eaeaea; border-radius: 5px; color: #626262; font-family: Tahoma; font-size: 14px; font-weight: 700; line-height: 11px; text-transform: uppercase; position: relative; margin-bottom: 10px; }

.file-input > [type='file'] { position: absolute; top: 0; left: 0; width: 100%; height: 100%; opacity: 0; z-index: 10; cursor: pointer; }

.file-input > .button { display: inline-block; cursor: pointer; background: #d6d6d6; float: right; padding: 28px 20px; border-radius: 2px; margin-right: 0px; }

.file-input:hover > .button { background: dodgerblue; color: white; }

.file-input > .label { color: #626262; text-transform: none; font-family: Tahoma; font-size: 14px; font-weight: 400; line-height: 18px; white-space: nowrap; opacity: 1; padding: 25px 20px; display: inline-block; }

.file-input.-chosen > .label { opacity: 1; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -10px; font: 0/0 a; text-shadow: none; color: transparent; height: 20px; background: none; left: 0; z-index: 1; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 30px; line-height: 30px; content: "prev"; display: inline-block; vertical-align: middle; font-size: 15px; color: #fff; background: #000; }

.slick-next { right: 0; left: auto; }
.slick-next:before { content: "next"; display: inline-block; }

.slick-dots { text-align: center; position: absolute; margin: -15px 0 0 0; top: 50%; transform: rotate(90deg); right: -85px; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { background-color: white; box-shadow: 0 0 9px #00c6ff; opacity: 1; }
.slick-dots button { display: block; width: 14px; height: 14px; opacity: 0.5; background-color: white; cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }

@font-face { font-family: ProximaNova-Regular; src: url("fonts/ProximaNova-Regular.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: ProximaNova-Bold; src: url("fonts/ProximaNova-Bold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: Akrobatblack; src: url("fonts/Akrobatblack.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: Akrobat-Regular; src: url("fonts/Akrobat-Regular.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: System; src: url("fonts/System.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: ProximaNova-Extrabld; src: url("fonts/ProximaNova-Extrabld.woff") format("woff"); font-weight: normal; font-style: normal; }
body { font-family: Arial, sans-serif; font-size: 12px; line-height: 1.4; }

.container { width: 1024px; margin: 0 auto; box-sizing: border-box; *zoom: 1; }
.container:after { content: " "; display: table; clear: both; }

.left { float: left !important; text-align: left; }

.right { float: right !important; text-align: right; }

.out { width: 100%; }

.header { background-image: linear-gradient(to top, #0087cd 0%, #00a8ff 43%, #00a8ff 100%); box-sizing: border-box; *zoom: 1; }
.header:after { content: " "; display: table; clear: both; }
.header .top__box { padding: 50px 0; box-sizing: border-box; *zoom: 1; }
.header .top__box:after { content: " "; display: table; clear: both; }
.header .top__box .inner { box-sizing: border-box; *zoom: 1; height: 50px; padding: 6px 0; }
.header .top__box .inner:after { content: " "; display: table; clear: both; }
.header .top__box .inner ul li { position: relative; display: inline-block; }
.header .top__box .inner ul li a { font-family: "ProximaNova-Extrabld", Arial, sans-serif; font-size: 22px; text-decoration: none; line-height: 41px; color: #fffefe; margin-left: 60px; }
.header .top__box .inner ul li a:hover { color: #006498; }
.header .header__bottom { box-sizing: border-box; *zoom: 1; }
.header .header__bottom:after { content: " "; display: table; clear: both; }
.header .header__bottom .col { float: left; width: 50%; }
.header .header__bottom .col .inner { box-sizing: border-box; *zoom: 1; padding: 15px 135px 135px 96px; background: url("../img/bg_slider.png") 0 0 no-repeat; }
.header .header__bottom .col .inner:after { content: " "; display: table; clear: both; }

.portfolio { box-sizing: border-box; *zoom: 1; padding: 60px 0; }
.portfolio:after { content: " "; display: table; clear: both; }
.portfolio h3 { color: #3c3c3c; font-family: "Akrobatblack", Arial, sans-serif; font-size: 48px; font-weight: 900; text-transform: uppercase; }
.portfolio .sub-title { color: #505050; font-family: "Akrobat-Regular", Arial, sans-serif; font-size: 22px; line-height: 28px; text-transform: uppercase; margin-bottom: 10px; }
.portfolio .item { float: left; width: 25%; }
.portfolio .item .box { background: #f1f1f1; margin-right: 10px; margin-bottom: 10px; }
.portfolio .item .box img { width: 100%; }
.portfolio .center { width: 100%; display: block; text-align: center; font-family: "ProximaNova-Regular", Arial, sans-serif; font-size: 14px; font-weight: 400; line-height: 24px; color: #3c3c3c; }
.portfolio .center a { color: #00a8ff; text-decoration: none; }
.portfolio .center a:hover { text-decoration: underline; }

.order { box-sizing: border-box; *zoom: 1; background: #f4f4f4; padding: 35px 0; background-size: 100% 100%; }
.order:after { content: " "; display: table; clear: both; }
.order .row { box-sizing: border-box; *zoom: 1; }
.order .row:after { content: " "; display: table; clear: both; }
.order .row .col { float: left; width: 50%; }
.order .row .col .h3 { color: #3c3c3c; font-family: "Akrobatblack", Arial, sans-serif; font-size: 48px; font-weight: 900; text-transform: uppercase; margin-bottom: 15px; line-height: 1; }
.order .row .col p { color: #3c3c3c; font-family: "ProximaNova-Regular", Arial, sans-serif; font-size: 16px; font-weight: 400; line-height: 30px; padding: 0 15px 0 0; }
.order .row .col p a { color: #00a8ff; text-decoration: none; }
.order .row .col p a:hover { text-decoration: underline; }
.order .row .col .form { padding: 10px 70px 0 52px; box-sizing: border-box; *zoom: 1; }
.order .row .col .form:after { content: " "; display: table; clear: both; }
.order .row .col .form .input-group { box-sizing: border-box; *zoom: 1; margin-bottom: 10px; }
.order .row .col .form .input-group:after { content: " "; display: table; clear: both; }
.order .row .col .form .input-group input[type="text"] { width: 100%; background: #eaeaea; border-radius: 5px; padding: 25px 20px; color: #626262; font-family: Tahoma; font-size: 14px; font-weight: 400; line-height: 18px; }
.order .row .col .form .input-group textarea { width: 100%; background: #eaeaea; border-radius: 5px; padding: 25px 20px; color: #626262; font-family: Tahoma; font-size: 14px; font-weight: 400; line-height: 18px; border: none; }
.order .row .col .form .input-group input[type="submit"] { background: #1dafed; padding: 24px 20px; border-radius: 5px; font-family: "Akrobatblack", Arial, sans-serif; text-transform: uppercase; color: #fff; }

.advantages { box-sizing: border-box; *zoom: 1; }
.advantages:after { content: " "; display: table; clear: both; }
.advantages .row { padding: 60px 0; box-sizing: border-box; *zoom: 1; }
.advantages .row:after { content: " "; display: table; clear: both; }
.advantages .row .h3 { color: #3c3c3c; font-family: "Akrobatblack", Arial, sans-serif; font-size: 48px; font-weight: 900; line-height: 1; text-transform: uppercase; margin-bottom: 35px; }
.advantages .row .col { float: left; width: 33.3%; position: relative; box-sizing: border-box; *zoom: 1; }
.advantages .row .col:after { content: " "; display: table; clear: both; }
.advantages .row .col .number { position: absolute; top: 0; left: 0; color: #00a8ff; font-family: "Akrobat-Regular", Arial, sans-serif; font-size: 88px; font-weight: 400; line-height: 60px; text-transform: uppercase; }
.advantages .row .col .content { padding-left: 90px; }
.advantages .row .col .content .name { color: black; font-family: "ProximaNova-Bold", Arial, sans-serif; font-size: 24px; font-weight: 700; line-height: 30px; }
.advantages .row .col .content .text { color: black; font-family: "ProximaNova-Regular", Arial, sans-serif; font-size: 18px; font-weight: 400; line-height: 24px; }

.footer { box-sizing: border-box; *zoom: 1; }
.footer:after { content: " "; display: table; clear: both; }
.footer .top-box { background: #00a8ff; box-sizing: border-box; *zoom: 1; }
.footer .top-box:after { content: " "; display: table; clear: both; }
.footer .top-box .inner { padding: 75px 70px; text-align: center; }
.footer .top-box .inner p { color: white; font-family: "ProximaNova-Bold", Arial, sans-serif; font-size: 24px; font-weight: 700; line-height: 30px; }
.footer .top-box .share { box-sizing: border-box; *zoom: 1; padding: 23px 0 0 0; }
.footer .top-box .share:after { content: " "; display: table; clear: both; }
.footer .top-box .share li { position: relative; display: inline-block; margin-right: 15px; }
.footer .top-box .share li .icon, .footer .top-box .share li [class^="icon-"], .footer .top-box .share li [class*=" icon-"] { margin-bottom: -3px; }
.footer .top-box .share li a { color: white; font-family: "ProximaNova-Bold", Arial, sans-serif; font-size: 30px; font-weight: 900; line-height: 34px; text-decoration: none; }
.footer .top-box .share li a:hover { text-decoration: underline; }
.footer .top-box .share li:nth-child(3) { margin-right: 30px; }
.footer .bottom-box { background: #171717; padding: 20px 0; box-sizing: border-box; *zoom: 1; }
.footer .bottom-box:after { content: " "; display: table; clear: both; }
.footer .bottom-box .copy { font-family: "ProximaNova-Regular", Arial, sans-serif; color: #00a8ff; font-size: 14px; font-weight: 400; line-height: 20px; }
.footer .bottom-box .footer__nav li { position: relative; display: inline-block; }
.footer .bottom-box .footer__nav li a { font-family: "ProximaNova-Regular", Arial, sans-serif; text-decoration: none; margin-right: 20px; font-size: 14px; font-weight: 400; line-height: 20px; color: #686868; }
.footer .bottom-box .footer__nav li a:hover { color: #fff; }
.footer .bottom-box .footer__nav li a:active { color: #fff; }


